/* eslint no-console: off */
/* global mode */

const LOGGER_DEBUG_REGEX = /_dev|__local|_test/
export const LOGGER_COLOR = {
  PRIMARY: 'background-color:#712cf9;color:white;font-weight:bold;padding:0 5px;',
  BLUE: 'background-color:#0d6efd;color:white;',
  INDIGO: 'background-color:#6610f2;color:white;',
  PURPLE: 'background-color:#6f42c1;color:white;',
  PINK: 'background-color:#d63384;color:white;',
  RED: 'background-color:#dc3545;color:white;',
  ORANGE: 'background-color:#fd7e14;color:white;',
  YELLOW: 'background-color:#ffc107;',
  GREEN: 'background-color:#198754;color:white;',
  TEAL: 'background-color:#20c997;color:white;',
  CYAN: 'background-color:#0dcaf0;',
  BLACK: 'background-color:#000;color:white;',
  WHITE: 'background-color:#fff;',
  GRAY: 'background-color:#f8f9fa;font-style:italic;padding:0 5px;'
}
export const LOGGER_TYPE = {
  DEBUG: 'debug',
  ERROR: 'error',
  WARN: 'warn',
  LOG: 'log',
  INFO: 'info'
}

export class Logger {
  static log(...args) {
    console.log(...args)
  }

  static warn(...args) {
    console.warn(...args)
  }

  static error(...args) {
    console.error(...args)
  }

  static info(...args) {
    console.info(...args)
  }

  static debug(...args) {
    if (LOGGER_DEBUG_REGEX.test(mode.env)) {
      console.debug(...args)
    }
  }

  static colorLog(module = chrome.runtime.getManifest().name, ...args) {
    console.log.apply(null, [`%c${module}`, LOGGER_COLOR.GRAY, ...args])
  }

  static colorWarn(module = chrome.runtime.getManifest().name, ...args) {
    console.warn.apply(null, [`%c${module}`, LOGGER_COLOR.YELLOW, ...args])
  }

  static colorError(module = chrome.runtime.getManifest().name, ...args) {
    console.error.apply(null, [`%c${module}`, LOGGER_COLOR.RED, ...args])
  }

  static colorInfo(module = chrome.runtime.getManifest().name, ...args) {
    console.info.apply(null, [`%c${module}`, LOGGER_COLOR.CYAN, ...args])
  }

  static colorDebug(module = chrome.runtime.getManifest().name, ...args) {
    if (LOGGER_DEBUG_REGEX.test(mode.env)) {
      console.debug.apply(null, [`%c${module}`, LOGGER_COLOR.GRAY, ...args])
    }
  }

  static color(module = chrome.runtime.getManifest().name, type = LOGGER_TYPE.DEBUG, color = LOGGER_COLOR.GRAY, ...args) {
    if (LOGGER_DEBUG_REGEX.test(mode.env)) {
      console[type].apply(null, [`%c${module}`, color, ...args])
    }
  }

  static group(...args) {
    console.group(...args)
  }

  static groupCollapsed(...args) {
    console.groupCollapsed(...args)
  }

  static groupEnd(...args) {
    console.groupEnd(...args)
  }
}
